import "./landing.css";

import logo2 from "../../images/Open source-cuate.svg";


import React from "react";

function landing() {
  return (
    <div className="container hero  ">
      
      <div className="row">
        <div className="box">
        <div className="col-md-6 col-12">
          <div className="left">
          <h1 className="text-light my-1 fw-bolder">
           <span>Your Insights :<br /> </span> 
            Everything Looks Better With Us
          </h1>
          <p className="text-light py-3">
          Your Insights is a platform where you can store your society's progress, important links and data everything in one space. We believe in the power of Engage, Enlighten, Encourage! Follow us in this journey of achieving great milestones.
          </p>
          
          <form className="d-flex m-media">
          
            <button class="btn" role="button" >
              Register
            </button>
          
          </form>
          
          </div>
         
</div>
 <img src={logo2} className="svg" />
         
        </div>
      </div>
    </div>
  );
}

export default landing
