import React from 'react';
import './services.css';

import finance from "../../images/financial-calculator.jfif";
import dashboard from "../../images/dashboard.png";
import todo from "../../images/to-do-app.png";
import sourcehub from "../../images/source hub.png";
import msgapp from "../../images/msg-app.png";
import socialapp from "../../images/social-app.png";

const Services = () => {
  return (
    <div className='services' id="service">
      <div className='heading1 text-center'>
        <h1>Our Services</h1>
      </div>
     
      <div className='category w-auto '>
        
        <div className="category1">
          <div className='img-box'>
          <img src={finance} className='finance' />
          </div>
          <div className='name'>
            <h5>To-Do App</h5>
          </div>
        </div>
        <div className="category1">
          <div className='img-box'>
          <img src={dashboard} className='finance' />
          </div>
          <div className='name'>
            <h5>Dashboard</h5>
          </div>
        </div>
      
        <div className="category1">
          <div className='img-box'>
          <img src={todo} className='finance' />
          </div>
          <div className='name'>
            <h5>Financial Calculator</h5>
          </div>
        </div>
</div>
<div className='categories'>
        <div className="category2">
          <div className='img-box1'>
          <img src={msgapp} className='finance' />
          </div>
          <div className='name1'>
            <h5>Chat App</h5>
          </div>
        </div>

        <div className="category2">
          <div className='img-box1'>
          <img src={sourcehub} className='finance' />
          </div>
          <div className='name1'>
            <h5>Source Hub</h5>
          </div>
        </div>
       
        <div className="category2">
          <div className='img-box1'>
          <img src={socialapp} className='finance' />
          </div>
          <div className='name1'>
            <h5>Social App</h5>
          </div>
        </div>

       </div>
</div>
     
  

  )
}

export default Services