
import './about.css';
import React from 'react';
import aboutimg from "../../images/Dark analytics-bro.svg"
const About = () => {
  return (
 <div id="about" className='container about'>
  <div className='heading'>
    <h1>About Us</h1>
  </div>
  <div className='about-box py-0'>

  <div className='about-img'>
  <img src={aboutimg} className='aboutimg' />
  </div>
  <div className='content px-0'> 
       <p>Your Insights allows you to develop a platform for adequately organising and managing your work. As the name implies, by utilising this platform, companies can effortlessly track their insights with a single click in the most flexible and user-friendly manner.
       Our mission is to create an effortless Website and Application like no other. Customer specification and customer satisfaction is our priority.
       Our vision is to build a perfect platform which can live up to all expectations of our customers. We aim to establish a bond of loyalty and trust between customers and us.
        </p>

   </div>
  </div>
 </div>
  )
}

export default About